'use client';

import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { personalInfo, skills, experience } from '@/lib/data/portfolio-data';
import Link from 'next/link';

interface CommandLine {
  id: number;
  command: string;
  output?: React.ReactNode;
  isTyping?: boolean;
}

export default function Home() {
  const [commandHistory, setCommandHistory] = useState<CommandLine[]>([]);
  const [currentCommand, setCurrentCommand] = useState('');
  const [commandIndex, setCommandIndex] = useState(0);

  // Predefined command sequence to auto-run on load
  const autoCommands = [
    { cmd: 'whoami', delay: 800 },
    { cmd: 'cat about.md', delay: 2000 },
    { cmd: 'ls skills/', delay: 3000 },
  ];

  useEffect(() => {
    // Auto-run the first command on mount
    if (commandIndex < autoCommands.length) {
      const timer = setTimeout(() => {
        executeCommand(autoCommands[commandIndex].cmd);
        setCommandIndex(commandIndex + 1);
      }, autoCommands[commandIndex].delay);

      return () => clearTimeout(timer);
    }
  }, [commandIndex]);

  const executeCommand = (cmd: string) => {
    const trimmedCmd = cmd.trim().toLowerCase();
    let output: React.ReactNode = null;

    switch (trimmedCmd) {
      case 'whoami':
        output = (
          <div className="space-y-2 text-secondary">
            <p className="text-accent font-bold">{personalInfo.name}</p>
            <p>{personalInfo.title}</p>
            <p className="text-sm">{personalInfo.subtitle}</p>
            <div className="flex gap-4 mt-3 text-sm">
              <a href={personalInfo.github} target="_blank" rel="noopener noreferrer" className="hover:text-accent transition-colors">
                GitHub →
              </a>
              <a href={personalInfo.linkedin} target="_blank" rel="noopener noreferrer" className="hover:text-accent transition-colors">
                LinkedIn →
              </a>
              <Link href="/contact" className="hover:text-accent transition-colors">
                Contact →
              </Link>
            </div>
          </div>
        );
        break;

      case 'cat about.md':
        output = (
          <div className="space-y-3 text-secondary text-sm leading-relaxed">
            <p>I'm a Backend Engineer specializing in Python (FastAPI, Flask) and Laravel, focused on building reliable APIs, automation workflows, and backend systems for SaaS and AI-powered products.</p>
            <p className="text-muted">I'm not a generalist — I'm strongest where companies feel the most pain: systems that move data, integrate platforms, automate operations, and scale without breaking.</p>
          </div>
        );
        break;

      case 'ls skills/':
        output = (
          <div className="grid grid-cols-2 md:grid-cols-3 gap-3 text-sm">
            {Object.entries(skills).map(([category, items]) => (
              <div key={category} className="space-y-1">
                <p className="text-accent font-mono">/{category}</p>
                <div className="text-secondary text-xs space-y-0.5 ml-3">
                  {items.map((skill, idx) => (
                    <p key={idx} className="hover:text-foreground transition-colors cursor-default">
                      {skill}
                    </p>
                  ))}
                </div>
              </div>
            ))}
          </div>
        );
        break;

      case 'cat experience.log':
        output = (
          <div className="space-y-4 text-sm">
            {experience.slice(0, 2).map((exp, idx) => (
              <div key={idx} className="border-l-2 border-accent/30 pl-4">
                <p className="text-accent font-bold">{exp.company}</p>
                <p className="text-secondary">{exp.title} | {exp.period}</p>
                <p className="text-muted text-xs mt-1">{exp.description}</p>
              </div>
            ))}
            <Link href="/work" className="text-accent hover:text-accent/80 text-xs inline-block mt-2">
              View full experience →
            </Link>
          </div>
        );
        break;

      case 'help':
        output = (
          <div className="text-secondary text-sm space-y-1">
            <p className="text-accent mb-2">Available commands:</p>
            <p><span className="text-foreground">whoami</span> - Display information about me</p>
            <p><span className="text-foreground">cat about.md</span> - Read my background</p>
            <p><span className="text-foreground">ls skills/</span> - List technical skills</p>
            <p><span className="text-foreground">cat experience.log</span> - View work history</p>
            <p><span className="text-foreground">clear</span> - Clear terminal</p>
          </div>
        );
        break;

      case 'clear':
        setCommandHistory([]);
        setCurrentCommand('');
        return;

      default:
        output = (
          <p className="text-red-400 text-sm">
            Command not found: {cmd}. Type 'help' for available commands.
          </p>
        );
    }

    const newCommand: CommandLine = {
      id: Date.now(),
      command: cmd,
      output: output,
    };

    setCommandHistory([...commandHistory, newCommand]);
    setCurrentCommand('');
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter' && currentCommand.trim()) {
      executeCommand(currentCommand);
    }
  };

  return (
    <div className="relative min-h-screen flex items-center justify-center overflow-hidden py-32 px-4">
      {/* Matrix-style background */}
      <div className="absolute inset-0 bg-gradient-to-b from-black via-[#001a00] to-black opacity-60"></div>

      {/* Animated grid background */}
      <div className="absolute inset-0 opacity-10">
        <div className="absolute inset-0" style={{
          backgroundImage: 'linear-gradient(rgba(0, 255, 65, 0.1) 1px, transparent 1px), linear-gradient(90deg, rgba(0, 255, 65, 0.1) 1px, transparent 1px)',
          backgroundSize: '50px 50px'
        }}></div>
      </div>

      {/* Terminal window */}
      <motion.div
        className="relative z-10 w-full max-w-5xl"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
      >
        {/* Terminal header */}
        <div className="glass-card rounded-t-2xl border-b border-accent/20">
          <div className="flex items-center justify-between px-4 py-3">
            <div className="flex items-center gap-2">
              <div className="w-3 h-3 rounded-full bg-red-500/80"></div>
              <div className="w-3 h-3 rounded-full bg-yellow-500/80"></div>
              <div className="w-3 h-3 rounded-full bg-green-500/80"></div>
            </div>
            <div className="text-secondary text-xs font-mono">
              moeez@portfolio:~$
            </div>
            <div className="w-16"></div>
          </div>
        </div>

        {/* Terminal body */}
        <div className="glass-card rounded-b-2xl p-6 md:p-8 min-h-[500px] max-h-[600px] overflow-y-auto font-mono">
          {/* Welcome message */}
          <div className="mb-6 text-sm text-secondary space-y-1">
            <p className="text-accent">Welcome to Moeez Rehman's Portfolio Terminal v1.0</p>
            <p>Type 'help' to see available commands.</p>
            <p className="text-muted text-xs mt-2">──────────────────────────────────────────────────</p>
          </div>

          {/* Command history */}
          <div className="space-y-4">
            <AnimatePresence>
              {commandHistory.map((cmd) => (
                <motion.div
                  key={cmd.id}
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.3 }}
                  className="space-y-2"
                >
                  <div className="flex items-center gap-2 text-sm">
                    <span className="text-accent">$</span>
                    <span className="text-foreground">{cmd.command}</span>
                  </div>
                  {cmd.output && (
                    <div className="ml-4 mb-4">
                      {cmd.output}
                    </div>
                  )}
                </motion.div>
              ))}
            </AnimatePresence>

            {/* Input prompt */}
            <div className="flex items-center gap-2 text-sm">
              <span className="text-accent">$</span>
              <input
                type="text"
                value={currentCommand}
                onChange={(e) => setCurrentCommand(e.target.value)}
                onKeyDown={handleKeyDown}
                className="flex-1 bg-transparent outline-none text-foreground font-mono placeholder-muted"
                placeholder="Type a command... (try 'help')"
                autoFocus
              />
              <span className="w-2 h-4 bg-accent animate-pulse"></span>
            </div>
          </div>

          {/* Quick action buttons */}
          <div className="mt-8 pt-6 border-t border-accent/10">
            <p className="text-muted text-xs mb-3">Quick actions:</p>
            <div className="flex flex-wrap gap-2">
              <button
                onClick={() => executeCommand('whoami')}
                className="text-xs px-3 py-1.5 rounded-lg bg-accent/10 hover:bg-accent/20 text-accent border border-accent/20 transition-all"
              >
                whoami
              </button>
              <button
                onClick={() => executeCommand('cat about.md')}
                className="text-xs px-3 py-1.5 rounded-lg bg-accent/10 hover:bg-accent/20 text-accent border border-accent/20 transition-all"
              >
                cat about.md
              </button>
              <button
                onClick={() => executeCommand('ls skills/')}
                className="text-xs px-3 py-1.5 rounded-lg bg-accent/10 hover:bg-accent/20 text-accent border border-accent/20 transition-all"
              >
                ls skills/
              </button>
              <button
                onClick={() => executeCommand('cat experience.log')}
                className="text-xs px-3 py-1.5 rounded-lg bg-accent/10 hover:bg-accent/20 text-accent border border-accent/20 transition-all"
              >
                cat experience.log
              </button>
              <button
                onClick={() => executeCommand('clear')}
                className="text-xs px-3 py-1.5 rounded-lg bg-red-500/10 hover:bg-red-500/20 text-red-400 border border-red-400/20 transition-all"
              >
                clear
              </button>
            </div>
          </div>
        </div>
      </motion.div>
    </div>
  );
}
